<template>
    <el-dialog :title="formTitle" :value="value" v-model="visible" @close="resetFormData">
        <el-form :model="formData" label-width="120px" :rules="formRules" ref="formData">
            <el-form-item label="推荐标题" prop="title">
                <el-input v-model.trim="formData.title" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="推荐类型" prop="type">
                <el-select v-model="formData.type" placeholder="请选择" disabled v-if="formData.id !=''">
                    <el-option label="商品" value="0"></el-option>
                    <el-option label="外链" value="1"></el-option>
                </el-select>
                <el-select v-model="formData.type" placeholder="请选择" v-else>
                    <el-option label="商品" value="0"></el-option>
                    <el-option label="外链" value="1"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="商品ID" prop="pid" v-if="formData.type==0">
                <el-input v-model.trim="formData.pid" auto-complete="off" placeholder="请输入商品ID"></el-input>
            </el-form-item>

            <el-form-item label="外链" prop="url" v-else>
                <el-input v-model.trim="formData.url" auto-complete="off" placeholder="请输入外链"></el-input>
            </el-form-item>

            <el-form-item label="推荐图片" prop="coverId">
                <el-upload class="avatar-uploader"
                           action="/ks3/image_upload"
                           :show-file-list="false"
                           :on-success="onSuccess">
                    <img v-if="formData.coverUrl" :src="formData.coverUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <el-form-item label="有效时间" required>
                <el-date-picker
                    v-model="formData.startTime"
                    type="datetime"
                    format="yyyy-MM-dd HH:mm:ss"
                    placeholder="选择日期时间">
                </el-date-picker>
                至
                <el-date-picker
                    v-model="formData.endTime"
                    format="yyyy-MM-dd HH:mm:ss"
                    type="datetime"
                    placeholder="选择日期时间">
                </el-date-picker>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="visible = false">取消</el-button>
            <el-button size="small" type="primary" @click.native="formSubmit" :loading="formLoading">提交</el-button>
        </div>
    </el-dialog>
</template>

<script type="es6">
    import util, {formatDate} from '../../../api/util'
    import {httpPost} from '../../../api/api';

    export default {
        name: 'vDetail',
        props: ['value', 'data'],
        data() {
            let check = (rule, value, callback) => {
                if (this.formData.type == '0') {
                    if (this.formData.pid == 0) {
                        callback(new Error('请输入商品ID'));
                    }
                } else {
                    if (this.formData.url == '') {
                        callback(new Error('请输入外链'));
                    }
                }
                callback();
            };
            return {
                value11: '',
                visible: false, //默认隐藏
                showLoading: false,
                formTitle: '新增推荐',
                formLoading: false,
                formRules: {
                    title: [
                        {required: true, message: '请输入推荐标题', trigger: 'blur'},
                        {min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur'}
                    ],
                    type: [
                        {required: true, message: '请输入推荐类型', trigger: 'blur'},
                    ],
                    pid: [
                        {validator: check, trigger: 'blur'}
                    ],
                    url: [
                        {validator: check, trigger: 'blur'}
                    ]
                },
                formData: {
                    id: '',
                    title: '',
                    type: '0',
                    pid: 0,
                    url: '',
                    coverUrl: '',
                    coverId: '',
                    startTime: '',
                    endTime: '',
                },
                inputVisible: false, //隐藏、显示关键字输入框
                inputValue: '',
                avatarLoading: false,
            }
        },
        computed: {
            detail() { //返回详情
                let _self = this;
                if (!_self.value) { // 弹窗不显示，不进行请求
                    return false;
                }
                if (_self.data.id && _self.visible) {
                    _self.formTitle = '编辑推荐';
                    _self.formData = _self.data;
                    if (_self.data.startTime > 0) {
                        _self.formData.startTime = new Date(Number(_self.data.startTime));
                    } else {
                        _self.formData.startTime = '';
                    }
                    if (_self.data.endTime > 0) {
                        _self.formData.endTime = new Date(Number(_self.data.endTime));
                    } else {
                        _self.formData.endTime = '';
                    }
                    _self.formData.type = _self.formData.type + '';
                }
            }
        },
        methods: {
            formSubmit() {
                let _self = this;
                let coverId = _self.formData.coverId;
                if (!coverId) {
                    _self.$message.error('请上传推荐图片');
                    return;
                }
                if (!_self.formData.startTime || !_self.formData.endTime) {
                    _self.$message.error('有效时间不能为空');
                    return;
                }
                let para = new Object();
                para.id = _self.formData.id;
                para.title = _self.formData.title;
                para.pid = _self.formData.pid;
                para.url = _self.formData.url;
                para.coverId = _self.formData.coverId;
                para.startTime = formatDate(_self.formData.startTime, 'yyyy-MM-dd hh:mm:ss');
                para.endTime = formatDate(_self.formData.endTime, 'yyyy-MM-dd hh:mm:ss');
                para.type = _self.formData.type;

                _self.$refs.formData.validate((valid) => {
                    if (valid) {
                        _self.formLoading = true;

                        httpPost('proRecSave', para, _self, function (res) {
                            _self.formLoading = false;
                            try {
                                _self.$message.success('提交成功');
                                _self.$refs['formData'].resetFields();
                                _self.visible = false;
                                _self.$emit('refresh');
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            onSuccess(res, file) {
                this.formData.coverUrl = URL.createObjectURL(file.raw);
                this.formData.coverId = res.data.id;
            },
            resetFormData() { //关闭表格弹窗，重置表格数据
                let _self = this;
                _self.formTitle = '新增推荐';
                _self.formData = {
                    id: '',
                    title: '',
                    type: '0',
                    pid: 0,
                    url: '',
                    coverUrl: '',
                    coverId: '',
                    startTime: '',
                    endTime: '',
                };
                _self.data = {};
            }
        },
        watch: {
            detail(val) { //监测详情变化
            },
            value(val) {
                this.visible = val;
            },
            visible(val) {
                this.$emit('input', val);
            }
        },
        mounted() {
            if (this.value) {
                this.visible = true;
            }
        }
    }
</script>



