<template>
    <section>
        <!--顶部工具条-->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.id" placeholder="请输入ID" icon="circle-close"
                              :on-icon-click="resetSearch"
                              @keyup.enter.native="fetchList"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="fetchList()">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="showForm('-1')">新增推荐</el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!--表格-->
        <el-table v-loading="tableLoading" class="table-expand" :data="tableList" stripe border
                  :max-height="tableHeight" style="width: 100%;">

            <el-table-column prop="id" label="id" width="100"></el-table-column>


            <el-table-column prop="coverUrl" label="推荐图" width="200">
                <template scope="scope">
                    <img v-if="scope.row.coverUrl != ''" style="width: 100%;vertical-align:middle;margin:10px auto;"
                         :src="scope.row.coverUrl" alt=""/>
                </template>
            </el-table-column>

            <el-table-column prop="title" label="推荐标题"></el-table-column>
            <el-table-column prop="pid" label="绑定商品ID"></el-table-column>
            <el-table-column prop="clickCount" label="点击数"></el-table-column>
            <el-table-column label="有效时间" width="350">
                <template scope="scope">
                    {{ scope.row.startTimeFormat}} - {{ scope.row.endTimeFormat}}
                </template>
            </el-table-column>

            <el-table-column label="操作" width="290">
                <template scope="scope">

                    <el-button-group class="mt-10">
                        <el-button size="small" @click="handlePosition(scope.row,1)">上移</el-button>
                        <el-button size="small" @click="handlePosition(scope.row,0)">下移</el-button>
                        <el-button size="small" @click="showForm(scope.row)">编辑</el-button>
                    </el-button-group>

                    <el-button-group class="mt-10">

                        <el-button type="success" size="small" @click="handleTop(scope.row)">置顶</el-button>

                        <el-button :type="scope.row.status == 1 ?  'success' : 'danger'" size="small"
                                   @click="handleStatus(scope.row)">
                            {{ scope.row.status == 1 ? '上架':'下架' }}
                        </el-button>

                        <el-button type="danger" size="small"
                                   @click="handleDel(scope.row)">
                            {{ scope.row.isDel == 1 ? '恢复' : '删除' }}
                        </el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="mt-10">
            <el-pagination style="float:right;" @current-change="handleCurrentChange"
                           :page-size="10" :current-page="page"
                           layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-col>

        <!--详情页-->
        <v-detail :data="data" v-model="isShowForm" v-on:refresh="fetchList"></v-detail>

    </section>
</template>

<script type="es6">
    import util, {formatDate} from '../../../api/util'
    import {httpGet, httpPost} from '../../../api/api';
    import vDetail from './recommendDetail.vue'

    export default {
        components: {
            vDetail
        },
        data() {
            return {
                filters: {
                    id: '',
                },
                total: 0, //表格列表数据总数
                page: 1, //当前页，默认为第一页
                tableHeight: '100%',
                tableLoading: false, //表格的loading符号
                tableList: [], //表格数据
                isShowForm: false, //显示、隐藏编辑页
                data: {},
            }
        },
        methods: {
            handleCurrentChange(val) { //翻页
                this.page = val;
                this.fetchList();
            },
            fetchList() { //获取列表
                let _self = this;
                _self.tableHeight = document.getElementById('container').clientHeight - 77 - 42 - 15;
                let para = {
                    offset: 0,
                    size: 10,
                    id: this.filters.id,
                };
                para.offset = (_self.page - 1) * para.size;
                _self.tableLoading = true;
                httpGet('proRecList', para, _self, function (res) {
                    _self.tableLoading = false;
                    try {
                        let {error, status, data} = res;
                        _self.total = data.totalElements;
                        _self.tableList = data.content.map(o => {
                            o.startTimeFormat = formatDate(new Date(Number(o.startTime)), 'yyyy-MM-dd hh:mm:ss');
                            o.endTimeFormat = formatDate(new Date(Number(o.endTime)), 'yyyy-MM-dd hh:mm:ss');
                            return o;
                        });
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            },
            resetSearch() {
                this.filters = {
                    id: '',
                };
                this.fetchList();
            },
            showForm(row) { //显示表单
                this.isShowForm = true;
                this.data = row;
            },
            handleDel(row) {
                let _self = this;
                if (row.status == 0) {
                    _self.$message.error('请先下架');
                    return;
                }
                _self.$confirm('此操作将删除该记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    _self.tableLoading = true;
                    let para = new Object();
                    para.id = row.id;
                    httpPost('proRecDelete', para, _self, function (res) {
                        try {
                            _self.$message.success('删除成功');
                            _self.fetchList();
                        } catch (error) {
                            util.jsErrNotify(error);
                        }
                    })
                }).catch(() => {
                });
            },
            handleTop(row) {
                let _self = this;
                let type = Number(row.isTop) == 0 ? 1 : 0;
                let para = new Object();
                para.id = row.id;
                para.type = type;
                httpPost('proRecTop', para, _self, function (res) {
                    try {
                        let {error, status, data} = res;
                        _self.$message.success('操作成功');
                        _self.fetchList();
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            },
            handleStatus(row) {
                let _self = this;
                let status = Number(row.status) == 0 ? 1 : 0;
                let para = new Object();
                para.id = row.id;
                para.type = status;
                httpPost('proRecOffLine', para, _self, function (res) {
                    try {
                        _self.$message.success('操作成功');
                        _self.fetchList();
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            },
            handlePosition(row, type) {
                let _self = this;
                let para = new Object();
                para.id = row.id;
                para.type = type;
                httpPost('proRecMove', para, _self, function (res) {
                    try {
                        _self.$message.success('操作成功');
                        _self.fetchList();
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            },
        },
        mounted() {
            this.fetchList();
        }
    }


</script>

<style>
    /*
        表格展开部分样式
    */
    .table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }

</style>



